import App from './client/dist/svelte-cli.js';

let timer, prevVideoID = null;

class AppInstance {
	constructor(target, props) {
		if (AppInstance.instance) {
			AppInstance.instance.app.$destroy();
		}
		AppInstance.instance = this;
		this.app = new App({
			target: target,
			props: props
		});
		return this;
	}
}

async function init() {
	const summaryApp = await getDOMElementRepeated('#summaryApp');
	const target = summaryApp || document.createElement('div');
	target.id = 'summaryApp';
	if (!summaryApp) {
		await appendAppToElement(target);
	}
	new AppInstance(target);
}

async function appendAppToElement(element) {
	if (await isNewYTVersion()) {
		const aboveTheFold = await getDOMElementRepeated('#secondary-inner > ytd-watch-metadata > #above-the-fold', 180);
		aboveTheFold?.parentNode?.appendChild(element);
	} else {
		element.style.marginBottom = '8px';
		element.style.marginTop = '0';
		const nextResults = await getDOMElementRepeated('.style-scope ytd-watch-next-secondary-results-renderer', 180);
		nextResults?.parentNode?.insertBefore(element, nextResults);
	}
}

async function getDOMElementRepeated(selector, maxAttempts = 15) {
	let element = document.querySelector(selector);
	let attempts = 0;
	return new Promise((resolve) => {
		const interval = setInterval(() => {
			if (element) {
				resolve(element);
				clearInterval(interval);
			}
			if (attempts === maxAttempts) {
				resolve(null);
				clearInterval(interval);
			}
			attempts++;
			element = document.querySelector(selector);
		}, 20);
	});
}

chrome.runtime.onMessage.addListener(async function(message) {
	if (message.event === 'show-button' && prevVideoID !== message.videoId) {
		prevVideoID = message.videoId;
		timer = setInterval(async () => {
			if (!document.querySelector('#watch-page-skeleton')) {
				clearInterval(timer);
				timer = null;
				await init();
			}
		}, 50);
	}
});

async function isNewYTVersion() {
	const relatedVideos = getDOMElementRepeated('#related > ytd-watch-next-secondary-results-renderer');
	const chipCloud = getDOMElementRepeated('yt-related-chip-cloud-renderer');
	const titleDescription = getDOMElementRepeated('#title > #description');
	const [chipCloudResult, titleDescriptionResult, relatedVideosResult] = await Promise.all([chipCloud, titleDescription, relatedVideos]);
	return (!chipCloudResult && !relatedVideosResult) || titleDescriptionResult;
}